.flexRow {
    display: flex;
    gap: 5px;
    font-size: 13px;
    font-weight: bold;


    .rowBlock {
        height: 40px;
        background-color: var(--bg3);
        box-shadow: 0px 0px 0px 1px var(--borders-soft) inset;
        border-radius: 5px;
        text-align: center;
        line-height: 38px;
    }


    .medium {
        width: 90px;
        flex-shrink: 0;

        &.colored {
            color: #fff;
        }
    }

    .mainField {

        input:focus {
            background-color: var(--bg4)
        }


        overflow-x: auto;
        flex-grow: 1;

        input {
            user-select: all;
            -webkit-user-select: auto;
            background: none;
            font-size: 13px;
            font-weight: bold;
            outline: none;
            color: var(--text);
            width: 100%;
            padding: 0 7px;
            border: none;
            text-align: center;
        }
                
        &::-webkit-scrollbar {
            height: 5px;
        }
            
        &::-webkit-scrollbar-thumb {
            background-color: var(--bg1);
            border-radius: 10px;
        }
    }

    .square {
        display: grid;
        place-items: center;
        width: 38px;
        flex-shrink: 0;
    }

    &:last-child div:first-of-type {
        border-radius: 5px 5px 5px 9px;
    }
    &:last-child div:last-of-type {
        border-radius: 5px 5px 9px 5px;
    }
}